<template>
  <div class="row justify-content-center">
    <div class="col-sm-8">
      <div class="row justify-content-center">
        <div class="col-sm-8">
          <div class="form-group row">
            <label for="disabledSelect">Properties Quick Select</label>
            <select v-model="selected" id="disabledSelect" class="form-control">
                    <option selected>Choose Property</option>
                    <option :disabled="property.header" :value="property" v-for="property in properties">{{property.name}}</option>
            </select>
          </div>
        </div>
        <div class="col-sm-4">
          <button type="button" class="btn btn-danger" @click="archive(selected)">Archive</button>
          <button type="button" class="btn btn-warning" @click="route(selected)">Edit</button>
        </div>
        <div class="col sm-10" v-show="isMessage">
          <div class="alert alert-success text-center" role="alert">
            <strong>{{message}}</strong>
          </div>
        </div>
      </div>

    </div>
    <div class="col-sm-8" v-for="property in properties">
      <div class="row" v-show="!property.header">
        <ul class="list-group">
          <div class="list-group-item flex-column align-items-start">
            <div class="d-flex w-300 justify-content-between">
              <h2 class="mb-1">{{property.name}}</h2>
              <small>{{property.currency}} {{property.salePrice}}</small>
            </div>
            <p>Type: <b>{{property.selected}}</b></p>
          </div>
          <div class="list-group-item price">
            <button type="button" class="btn btn-danger" @click="archive(property)">Archive</button>
            <button type="button" class="btn btn-warning" @click="route(property)">Edit</button>
          </div>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'adminPropList',
    data() {
      return {
        selected: {}
      }
    },
    mounted() {
      this.$store.dispatch('getAllPropertiesShort')
    },
    computed: {
      message() {
        return this.$store.state.message
      },
      isMessage() {
        return this.message != ""
      },
      properties() {
        if (this.$store.state.properties.length <= 0) {
          return []
        }
        let data = []
        let foundTypes = {}
        this.$store.state.properties.sort(function (a, b) {
          var nameA = a.selected.toUpperCase(); // ignore upper and lowercase
          var nameB = b.selected.toUpperCase(); // ignore upper and lowercase
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }

          // names must be equal
          return 0;
        })
        this.$store.state.properties.forEach(function (element) {
          let type = element.selected
          if (!foundTypes[type]) {
            data.push({
              header: true,
              name: "Type: " + type
            })
            foundTypes[type] = true
          }
          data.push(element)
        }, this);
        return data
      }
    },
    methods: {
      route(prop) {
        this.$router.push({
          name: 'AdminPropEdit',
          params: {
            id: prop._id
          }
        })
      },
      archive(prop) {
        this.$store.dispatch('archiveProp', prop._id)
        this.$store.dispatch('getAllPropertiesShort')
      }

    }
  }

</script>

<style scoped>
  h2 {
    color: black;
  }

  .list-group {
    width: 100%;
    margin-bottom: 5px;
    color: black;
  }

</style>



