import axios from 'axios'
import router from '../router'
import Vue from 'vue'
import Vuex from 'vuex'
import Carousel from 'vue-carousel-3d'

Vue.use(Vuex)
Vue.use(Carousel)


let api = axios.create({
  baseURL: 'https://rland.herokuapp.com/api/',
  timeout: 10000,
  withCredentials: true
})

let auth = axios.create({
  baseURL: 'https://rland.herokuapp.com/',
  timeout: 2000,
  withCredentials: true
})

// REGISTER ALL DATA HERE
let state = {
  error: {},
  properties: [],
  property: {},
  user: {},
  viewUser: {},
  users: [],
  // auth: {},
  searchProperties: {},
  allCords: [],
  message: "",
  errorMessage: ""
}

let handleError = (state, err) => {

  state.error = err
  console.log(err)
}

export default new Vuex.Store({
  // ALL DATA LIVES IN THE STATE
  state,
  //Mutations are the only thing alowed to update the store directly through store.propName

  mutations: {

    setProperty(state, property) {
      state.property = property
    },
    setLogin(state, user) {

      state.user = user
      if (!user.admin) {
            
        router.push('/')
      } else {

        router.push('/admin')

      }
    },
    setRegister(state, user) {
      state.user = user
    },
    setAuth(state, auth) {
      state.user = auth
    },
    setUsers(state, data) {
      state.users = data
    },
    setUser(state, data) {
      state.viewUser = data
    },
    setProperties(state, properties) {
      console.log("Setting Properties", properties)
      state.properties = properties;
    },
    setSearchProperties(state, searchProperties) {
      state.searchProperties = searchProperties;
    },
    setAllProperties(state, properties) {
      state.properties = properties
    },
    setHome(state) {
      router.push("/")
    },
    setAllCords(state, data) {
      state.allCords = data
    },
    setMessage(state, data) {
      state.message = data
    }
  },

  // ACTIONS ARE RESPONSIBLE FOR MANAGING ALL ASYNC REQUESTS
  // Dispatch fires actions, commit fires mutations
  actions: {
    // getProperties({commit, dispatch}) {
    //   api('properties')
    //     .then(res => {
    //       commit('setProperties', res.data.data)
    //     })
    //     .catch(handleError)
    // },
    archiveProp({
      commit,
      dispatch
    }, id) {
      api.put('setstate/'+id, {archived: true})
        .then(res => {
          commit('setMessage', "Prop has been archived!")
        })
        .catch(handleError)

    },
    getAllUsers({
      commit,
      dispatch
    }) {
      api('allusers/')
        .then(res => {
          commit('setUsers', res.data.data)
        })
        .catch(handleError)
    },
    getUser({
      commit,
      dispatch
    }, id) {
      api('getuser/' + id)
        .then(res => {
          console.log("viewuser", res.data.data)
          commit('setUser', res.data.data)
        })
        .catch(handleError)
    },
    editUser({
      commit,
      dispatch
    }, id, data) {
      api('updateuser/' + id, data)
        .then(res => {
          console.log("viewuser", res.data.data)
          commit('setUser', res.data.data)
        })
        .catch(handleError)
    },
    getAllCords({
      commit,
      dispatch
    }) {
      api('allcords/')
        .then(res => {
          let data = []
          res.data.data.forEach(v => {
            data.push({
              lat: parseInt(v.lat),
              lng: parseInt(v.long)
            })
          })
          commit('setAllCords', data)
        })
        .catch(handleError)
    },
    getAllProperties({
      commit,
      dispatch
    }, id) {

      api('properties')
        .then(res => {
          commit('setProperties', res.data.data)
        })
        .catch(handleError)
    },

    getProperty({
      commit,
      dispatch
    }, id) {

      api('propertyinfo/' + id)
        .then(res => {
          commit('setProperty', res.data.data)
        })
        .catch(handleError)
    },

    searchProperties({
      commit,
      dispatch
    }, query) {
      api.post('propertysearch', query)
        .then(res => {
          commit('setProperties', res.data.data)
          router.push({name:"Search"})
        })
        .catch(handleError)
    },

    editProperty({
      commit,
      dispatch
    }, property) {

      api.put('properties/' + property._id, property)
        .then(res => {

          dispatch('getAllProperties')
        })
        .catch(handleError)
    },
    createProperty({
      commit,
      dispatch
    }, property) {

      api.post('properties/', property)
        .then(res => {
            console.log(res)
          dispatch('getAllProperties')
        })
        .catch(handleError)
    },

    removeProperty({
      commit,
      dispatch
    }, property) {
      api.delete('properties/' + property._id)
        .then(res => {
          dispatch('getAllProperties')
        })
        .catch(handleError)
    },
    getAllProperties({
      commit,
      dispatch
    }) {

      api.get('/properties')
        .then(res => {

          commit("setAllProperties", res.data.data)
        })
    },
    getAllPropertiesShort({
      commit,
      dispatch
    }) {

      api.get('/propertiesshort')
        .then(res => {

          commit("setAllProperties", res.data.data)
        })
    },
    getLakeFrontProperties({
      commit,
      dispatch
    }) {

      api.get('lakefront')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getRiverFrontProperties({
      commit,
      dispatch
    }) {

      api.get('riverfront')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getIslandsProperties({
      commit,
      dispatch
    }) {

      api.get('island')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getHuntingLandProperties({
      commit,
      dispatch
    }) {

      api.get('huntingland')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getRemoteAccessProperties({
      commit,
      dispatch
    }) {

      api.get('remoteaccess')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getCottageandCabinProperties({
      commit,
      dispatch
    }) {

      api.get('cottage&cabins')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getDevelopmentandResortsProperties({
      commit,
      dispatch
    }) {

      api.get('development&resorts')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    getUnorganizedTownshipProperties({
      commit,
      dispatch
    }) {

      api.get('unorganizedtownships')
        .then(res => {

          commit('setProperties', res.data.data)
        }).catch(handleError)


    },
    login({
      commit,
      dispatch
    }, user) {

      auth.post('login', user)
        .then(res => {

          if (res.data.data.error) {

            handleError(res.data.data.error)
          }

          commit('setLogin', res.data.data)

        })
        .catch(err => {

          handleError(err)
        })
    },
    reset({
      commit,
      dispatch
    }, user) {

      auth.post('reset', user)
        .then(res => {

          if (res.data.error) {
            handleError(res.data.error)
          }
          console.log("resp", res.data)
          commit('setMessage', res.data.message)
          commit('setLogin', res.data.data)
          router.push("/")
        })
        .catch(err => {

          handleError(err)
        })
    },
    resetConfirm({
      commit,
      dispatch
    }, user) {

      auth.post('checkreset', user)
        .then(res => {

          if (res.data.error) {
            handleError(res.data.error)
          }
          console.log("resp", res.data)
          commit('setMessage', res.data.message)

        })
        .catch(err => {

          handleError(err)
        })
    },

    register({
      commit,
      dispatch
    }, user) {
      auth.post('register', user)
        .then(res => {
          console.log(res)
          if (res.data.error) {
            return handleError(res.data.error)
          }
          //LETS REDIRECT THE PAGE
          commit('setRegister', res.data.data)
          router.push('/Home')
        })
        .catch(err => {

          handleError(err)
        })
    },

    getAuth({
      commit,
      dispatch
    }) {

      auth('authenticate')
        .then(res => {

          commit('setAuth', res.data.data)
          // router.push('/Home')
        }).catch(err => {
          // router.push('/Home')
        })
    },
    upload({
      commit,
      dispatch
    }, formData, callback) {
        return auth.post("/photos/upload",formData)
        // get data
        .then(x => x.data)
        // add url field
        .then(x => {
            console.log(x)
            x.map(img => Object.assign({},img, { url: `${BASE_URL}/images/${img.id}` }));
            callback(x)
        })
    },

    getImage({
      commit,
      dispatch
    }, file) {

      return new Promise((resolve, reject) => {

        const fReader = new FileReader();
        const img = document.createElement('img');

        fReader.onload = () => {

          img.src = fReader.result;
          resolve(dispatch("getBase64Image", img));
        }

        fReader.readAsDataURL(file);
      })
    },

    getBase64Image({
      commit,
      dispatch
    }, img) {

      const canvas = document.createElement('canvas');
      canvas.width = img.width;
      canvas.height = img.height;

      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0);

      const dataURL = canvas.toDataURL('image/png');

      return dataURL;
    },

    returnHome({
      commit,
      dispatch
    }) {
      commit("setHome")
    },



    clearError({
      commit,
      dispatch
    }) {
      state.error = {}
    }
  }
})



