<template>
  <div class="property">
    <div class="container-fluid property">
      <div class="row">
        <div class="col-12">
          <h1 class="siteName"><i>Recreationland.net</i></h1>

          <div class="button-container">
            <div class="buttons t-shadow">
              <div class="button">
                <router-link to="/contact">
                  <button>
                        <i class="fa fa-fw fa-envelope fa-2x t-shadow"></i>
                    </button>
                </router-link>
                <h6>Contact Us</h6>
              </div>

              <div class="button">
                <button><a href="#types">
                      <i class="fa fa-fw fa-home fa-2x t-shadow"></i></a>
                  </button>
                <h6>Properties</h6>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>




    <div class="container-fluid">
      <div class="row justify-content-center">
      </div>
      <div v-if="!this.user" class="row justify-content-center sticky-top">
        <div class="alert alert-info col-8 clearfix" role="alert">
          <div class="row justify-content-center">
            <div class="col-8">
              <strong>Heads up!</strong> Signup for free or login to see additional information
            </div>
            <div class="col">
              <div class="row" role="group" aria-label="Basic example">
                <router-link class="col" to="/login">
                  <button type="button" id="login-button" class="btn btn-primary">Login</button>
                </router-link>
                <router-link class="col" to="/register">
                  <button type="button" id="register-button" class="btn btn-secondary">Register</button>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row no-gutters justify-content-center property_holder" id="property-details">
        <div class="col-12">
          <ul class="list-group">
            <div class="list-group-item flex-column align-items-start">
              <div class="d-flex w-100 justify-content-between">
                <h2 class="mb-1">{{property.name}}</h2>
                <small>{{getCreated}} days ago</small>
              </div>
              <p class="mb-1">{{getShorDesc}}</p>
              <a href="#" class="highlight" @click.prevent="readMore">Read more</a>
            </div>
            <div class="list-group-item price">
              Price: {{property.currency}} {{property.listPrice}} 
            </div>
            <div class="list-group-item flex-column align-items-start">
              <div class="d-flex w-100 justify-content-between">
                <h6 class="mb-1">Lot Size</h6>
                <small>{{property.lotSize}}</small>
              </div>
            </div>
          </ul>
        </div>
        <div class="col-sm-12 clearfix">
          <div class="image-loop">
            <div class="property-images">
              <swiper :options="swiperOption">
                <swiper-slide v-for="slide in property.uploadedImages">
                  <img class="img-fluid" :src="slide.url" alt="">

                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
                <div class="swiper-button-prev" slot="button-prev"></div>
                <div class="swiper-button-next" slot="button-next"></div>
                <div class="swiper-scrollbar" slot="scrollbar"></div>
                <div class="swiper-pagination" slot="pagination"></div>
              </swiper>
              <!-- <div id="carouselExampleSlidesOnly" class="carousel slide" data-ride="carousel">
                                <div class="carousel-inner" role="listbox">
                                  <div class="carousel-item active">
                                    <img class="d-block img-fluid" :src="property.previewImage" alt="First slide">
                                  </div>
                                  <div v-for="img in property.uploadedImages" class="carousel-item">
                                    <img id="property-image" class="d-block img-fluid" :src="img" alt="Second slide">
                                  </div>
                                </div>
                              </div> -->
              <!-- <img v-for="image in property.uploadedImages" class="col-3" :src="image" alt="sorry :("> -->
            </div>
          </div>
        </div>
        <div class="col-sm-12">
          <div class="list-group">
            <div class="list-group-item justify-content-between">
              WaterFront: {{property.frontage}}
            </div>
            <div class="list-group-item justify-content-between">
              Power: {{property.power}}
            </div>
            <div class="list-group-item justify-content-between">
              Water: {{property.water}}
            </div>
            <div class="list-group-item justify-content-between">
              Sewer: {{property.sewer}}
            </div>
            <div class="list-group-item justify-content-between">
              Year: {{property.year}}
            </div>
          </div>
        </div>
        <div class="col-sm-12">
          <ul class="list-group">
            <div class="list-group-item justify-content-between" id="property_about">
              About: {{property.description}}
            </div>
          </ul>
        </div>
        <div class="col-12" v-if="this.user">
          <ul class="list-group">
            <div class="list-group-item justify-content-between">
              Cords: {{property.lat}}, {{property.long}}
            </div>
            <div class="list-group-item justify-content-between">
              Mineral Rights: {{property.mineralRights}}
            </div>
            <div class="list-group-item justify-content-between">
              Survey: {{property.survey}}
            </div>
            <div class="list-group-item justify-content-between">
              Title: {{property.title}}
            </div>
            <div class="list-group-item justify-content-between">
              Zoning: {{property.zoning}}
            </div>
            <div class="list-group-item justify-content-between">
              Legal: {{property.legalInformation}}
            </div>
            <div class="list-group-item justify-content-between">
              Direction to Property: {{property.directionsToProp}}
            </div>
          </ul>
        </div>
      </div>

      <!-- Active Property: {{this.property}} -->
      <div class="row">
        <div class="col-12">
          <div class="google-maps">
            <div v-if="property.lat && property.long">
              <g-map :lat="property.lat" :long="property.long"></g-map>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import GMap from './Map'
  import {
    swiper,
    swiperSlide
  } from 'vue-awesome-swiper'


  export default {
    name: 'property',
    data() {
      return {
        realIndex: 0,
        notNextTick: true,
        swiperOption: {
          autoHeight: true,
          autoplay: 3500,
          setWrapperSize: true,
          pagination: '.swiper-pagination',
          paginationClickable: true,
          observeParents: true,
          direction: 'horizontal',
          grabCursor: true,
          pagination: '.swiper-pagination',
          paginationClickable: true,
          prevButton: '.swiper-button-prev',
          nextButton: '.swiper-button-next',
          pagination: '.swiper-pagination',
          scrollbar: '.swiper-scrollbar',
        },
      }
    },
    methods: {
      findPos(obj) {
        var curtop = 0;
        if (obj.offsetParent) {
          do {
            curtop += obj.offsetTop;
          } while (obj = obj.offsetParent);
          return [curtop];
        }
      },
      readMore() {
        window.scroll(0, this.findPos(document.getElementById("property_about")));
      }
    },
    mounted() {


      this.$store.dispatch('getAuth')

      this.$store.dispatch("getProperty", this.$route.params.id)
    },
    computed: {
      getCreated() {
        var ONE_DAY = 1000 * 60 * 60 * 24

        // Convert both dates to milliseconds
        var date1_ms = this.$store.state.property.created
        let now = new Date()
        var date2_ms = now.getTime()

        // Calculate the difference in milliseconds
        var difference_ms = Math.abs(date1_ms - date2_ms)

        // Convert back to days and return
        return Math.round(difference_ms / ONE_DAY)
      },

      property() {
        return this.$store.state.property
      },
      user() {
        return this.$store.state.user
      },
      swiper() {
        return this.$refs.mySwiper.swiper
      },
      getShorDesc() {
        if (!this.$store.state.property.description) {
          return ""
        }
        var trimmedString = this.$store.state.property.description.substr(0, 250);

        //re-trim if we are in the middle of a word
        trimmedString = trimmedString.substr(0, Math.min(trimmedString.length, trimmedString.lastIndexOf(" ")))
        trimmedString = trimmedString + "..."
        return trimmedString
      }
    },
    components: {
      GMap,
      swiper,
      swiperSlide
    }
  }

</script>

<style scoped>
  .property {
    background-color: #3b423a;
    width: cover;
  }

  .property_holder {
    background-color: darkgray;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
  }

  .price {
    background-color: #8a1e31;
    color: white;
    font-weight: bold;
  }

  .highlight {
    color: #8a1e31;
    font-weight: bold;
  }

  h1 {
    font-family: 'Abril Fatface', cursive;
    font-size: 52px;
    text-shadow: 2px 2px 2px #fff;
    color: red;
    margin-left: 40px;
    margin-top: 20px;
    padding-left: 30px;
    padding-top: 35px;
  }

  img {
    max-height: 75%;
    height: 20rem;
    width: 100%;
  }

  #property-details {
    border: 2px solid brown;
  }

  .google-maps {
    height: 70%;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
  }

  .jumbotron {
    height: 30px;
  }

  .siteName {
    height: 120px;
    background-color: #be9d6d;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 10px;
  }

  .list-group-item {
    font-weight: bold;
  }

  .button {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: space-around;
    align-content: center;
    align-items: center;
    opacity: 1;
    z-index: 999;
  }

  .button-container {
    display: flex;
    margin-left: 75%;
    margin-top: -115px;
    justify-content: space-around;
    align-content: center;
    align-items: center;
    opacity: 1;
  }

  .button-container .buttons {
    display: flex;
    z-index: 999;
  }

  .button-container .buttons .button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
    font-family: Karma;
    text-shadow: 1px 1px 1px #000;
  }

  .button-container button {
    background: transparent;
    border-radius: 100%;
    border: 2px solid rgba(255, 255, 255, .5);
    color: white;
    font-size: 1.5rem;
    margin: 0 1.5rem;
    display: block;
    height: 80px;
    width: 80px;
    transition: all .3s linear;
  }

  .button-container button:hover {
    background: rgba(200, 200, 200, .5);
    border: 2px solid rgba(200, 200, 200, .5);
  }

  .button-container .buttons .button:hover {
    cursor: pointer;
  }

  .fa-home {
    color: white;
  }

</style>



