  </div>
</template>


<script>
  import myFooter from './myFooter'
  import PropertyPreview from "./PropertyPreview"
  export default {
    name: 'cottagecabins',
    data() {
      return {

      }

    },
    mounted() {
      this.$store.dispatch('getCottageCabinsProperties')
    },
    computed: {
      properties() {
        return this.$store.state.properties
      },
    },
    methods: {},
    components: {
       
      PropertyPreview
    }
  }

</script>


<style scoped>
  .property {
    background-color: #3b423a;
    width: cover;
  }

  .logo {
    font-family: 'Abril Fatface', cursive;
    font-size: 3rem;
    text-shadow: 2px 2px 2px #fff;
    color: red;
  }

  .jumbotron {
    background-color: #be9d6d;
    height: 9rem;
    margin-top: 1.5rem;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 2rem;
    padding-top: 2rem;
    border-radius: 10px;
    
  }
  /* .container{
  background-color: #3b423a;
} */

</style>



