<template>
  <div class="row justify-content-center">
    <div v-if="submitted" class=" col-sm-8 success">
      <div class="alert alert-success" role="alert">
        <strong>Success!</strong> user has been created successfully.
      </div>
    </div>
    <div v-if="saved" class=" col-sm-8 success">
      <div class="alert alert-success" role="alert">
        <strong>Success!</strong> user has been updated successfully.
      </div>
    </div>
    <div class="col-sm-6 holder">

      <div id="form-panel">
        <div id="signinbox">
          <form action="#" id="signinform">
            <div class="form-group row">
              <label for="email" class="col-sm-2 col-form-label">Name</label>
              <div class="col-sm-10">
                <input v-model="user.name" type="text" class="form-control" id="email" autocomplete="off">
              </div>
            </div>
            <div class="form-group row">
              <label for="email" class="col-sm-2 col-form-label">Email</label>
              <div class="col-sm-10">
                <input v-model="user.email" type="email" class="form-control" id="email" autocomplete="off">
              </div>
            </div>
            <div class="form-group row" v-show="!isEditing">
              <label for="email" class="col-sm-2 col-form-label">Password</label>
              <div class="col-sm-10">
                <input v-model="user.password" type="email" class="form-control" id="email" autocomplete="off">
              </div>
            </div>
            <div class="form-group row" v-show="!isEditing">
              <label for="email" class="col-sm-2 col-form-label">Confirm Password</label>
              <div class="col-sm-10">
                <input v-model="user.confim" type="email" class="form-control" id="email" autocomplete="off">
              </div>
            </div>
            <div class="form-group row">
              <div class="form-check col-sm-10">
                <label class="form-check-label">
                                    <input v-model="user.admin" type="checkbox" class="form-check-input"> Admin
                                </label>
              </div>
            </div>
            <input @click.prevent="createProperty" type="submit" name="loginbtn" id="submitlogin" value="CREATE" class="btn" tabindex="3"
              v-show="!isEditing">
            <input @click.prevent="editProperty" type="submit" name="loginbtn" id="submitlogin" value="EDIT" class="btn" tabindex="3"
              v-show="isEditing">
          </form>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'adminCreateUser',
    beforeRouteUpdate(to, from, next) {
      // react to route changes...
      // don't forget to call next()
      console.log(to)
      next(vm => {
        // access to component instance via `vm`
        console.log("this", vm)
      })
    },
    data() {
      return {
        submitted: false,
        saved: false,
      }
    },
    created() {
      if (this.$route.params.id) {
        this.$store.dispatch("getUser", this.$route.params.id)
       //this.prop = this.$store.state.property
      }
    },
    mounted() {
      if (this.$route.params.id) {
        //this.prop = this.$store.state.property
        this.$store.dispatch("getUser", this.$route.params.id)

      }
    },
    computed: {
      user() {
        if(!this.isEditing){
            return {}
        }
        //this.prop = this.$store.state.property
        return this.$store.state.viewUser
      },
      isEditing() {
        return this.$route.params.id != null
      }
    },
    methods: {
      route(route) {
        this.$router.push(route)
      },
      onFileChange(e) {

        var files = e.target.files || e.dataTransfer.files;
        if (!files.length)
          return;
        for (var i = 0; i < files.length; i++) {
          var file = files[i];
          this.createImage(file)

        }
      },
      createImage(file) {

        var image = new Image();
        var reader = new FileReader();
        var vm = this;

        reader.onload = (e) => {
          vm.uploadedImages.push(e.target.result)
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      },
      removeImage: function (e) {
        this.image = '';
      },

      reset() {

        // reset form to initial state
        this.currentStatus = STATUS_INITIAL;
        this.uploadedFiles = [];
        this.uploadError = null;
      },
      save(formData) {

        // upload data to the server
        this.currentStatus = STATUS_SAVING;

        this.$store.dispatch("upload", formData)
          .then(x => {

            this.uploadedFiles = [].concat(x);
            this.currentStatus = STATUS_SUCCESS;
          })
          .catch(err => {
            this.uploadError = err.response;
            this.currentStatus = STATUS_FAILED;
          });
      },
      filesChange(fieldName, fileList) {

        // handle file changes
        var formData = new FormData();

        if (!fileList.length) return;

        // append the files to FormData
        Array
          .from(Array(fileList.length).keys())
          .map(x => {
            formData.append(fieldName, fileList[x], fileList[x].name);
          });

        // save it
        this.save(formData);
      },
      toggleSubmit() {
        this.propertyUpload = true;
        this.propertyEdit = false;
      },

      toggleEdit() {
        this.propertyUpload = false;
        this.propertyEdit = true;

      },
      createProperty() {

        let newProperty = {
          selected: this.prop.selected.toLowerCase().split(" ").join(""),
          name: this.prop.name,
          currency: this.prop.currency,
          listPrice: this.prop.listPrice,
          lotSize: this.prop.lotSize,
          long: this.prop.long,
          lat: this.prop.lat,
          uploadedImages: this.prop.uploadedImages,
          toggle: this.prop.toggle,
          description: this.prop.description,
          taxes: this.prop.taxes,
          year: this.prop.year,
          zoning: this.prop.zoning,
          roadfrontage: this.prop.roadFrontage,
          survey: this.prop.survey,
          seasonalAccess: this.prop.seasonalAccess,
          power: this.prop.power,
          treeRights: this.prop.treeRights,
          frontage: this.prop.frontage,
          structure: this.prop.structure,
          usage: this.prop.usage,
          mineralRights: this.prop.usage,
          unorganizedTownship: this.prop.unorganizedTownship,
          legal: this.prop.legal,
          title: this.prop.title,
          bodyOfWaterName: this.prop.bodyOfWaterName,
          directionsToProp: this.prop.directionsToProp,
          water: this.prop.water,
          sewer: this.prop.sewer,
          legalInformation: this.prop.legalInformation,
          shortDescription: this.prop.description.slice(0, 100),
          conditionalOffer: this.prop.conditionalOffer,
          isFeature: this.prop.isFeature,
          propertyDisclosure: this.prop.propertyDisclosure,
          use: this.prop.use,
          basement: this.prop.basement,
          siteInfluences: this.prop.siteInfluences,
          numStories: this.prop.numStories,
          buildingDiv: this.prop.buildingDiv,
          totalArea: this.prop.totalArea,
          area: this.prop.area,
          officeArea: this.prop.officeArea,
          whseArea: this.prop.whseArea,
          ceiling: this.prop.ceiling,
          construction: this.prop.construction,
          heatAir: this.prop.heatAir,
          whseHeatAir: this.prop.whseHeatAir,
          fuel: this.prop.fuel,
          loading: this.prop.loading,
          numRentalUnits: this.prop.numRentalUnits,
          features: this.prop.features,
          mortage: this.prop.mortage,
          exclusions: this.prop.exclusions,
          remarks: this.prop.remarks,
          agreement: this.prop.agreement,
          occupants: this.prop.occupants,
          howShown: this.prop.howShown,
          saleDate: this.prop.saleDate,
          accessRoad: this.prop.accessRoad
        }

        this.submitted = true;
        debugger

        this.$store.dispatch('createProperty', newProperty)
      },
      editProperty() {

        let newProperty = {
          _id: this.$route.params.id,
          selected: this.prop.selected.toLowerCase().split(" ").join(""),
          name: this.prop.name,
          currency: this.prop.currency,
          listPrice: this.prop.listPrice,
          lotSize: this.prop.lotSize,
          long: this.prop.long,
          lat: this.prop.lat,
          uploadedImages: this.prop.uploadedImages,
          toggle: this.prop.toggle,
          description: this.prop.description,
          taxes: this.prop.taxes,
          year: this.prop.year,
          zoning: this.prop.zoning,
          roadfrontage: this.prop.roadFrontage,
          survey: this.prop.survey,
          seasonalAccess: this.prop.seasonalAccess,
          power: this.prop.power,
          treeRights: this.prop.treeRights,
          frontage: this.prop.frontage,
          structure: this.prop.structure,
          usage: this.prop.usage,
          mineralRights: this.prop.usage,
          unorganizedTownship: this.prop.unorganizedTownship,
          legal: this.prop.legal,
          title: this.prop.title,
          bodyOfWaterName: this.prop.bodyOfWaterName,
          directionsToProp: this.prop.directionsToProp,
          water: this.prop.water,
          sewer: this.prop.sewer,
          legalInformation: this.prop.legalInformation,
          shortDescription: this.prop.description.slice(0, 100),
          conditionalOffer: this.prop.conditionalOffer,
          isFeature: this.prop.isFeature,
          propertyDisclosure: this.prop.propertyDisclosure,
          use: this.prop.use,
          basement: this.prop.basement,
          siteInfluences: this.prop.siteInfluences,
          numStories: this.prop.numStories,
          buildingDiv: this.prop.buildingDiv,
          totalArea: this.prop.totalArea,
          area: this.prop.area,
          officeArea: this.prop.officeArea,
          whseArea: this.prop.whseArea,
          ceiling: this.prop.ceiling,
          construction: this.prop.construction,
          heatAir: this.prop.heatAir,
          whseHeatAir: this.prop.whseHeatAir,
          fuel: this.prop.fuel,
          loading: this.prop.loading,
          numRentalUnits: this.prop.numRentalUnits,
          features: this.prop.features,
          mortage: this.prop.mortage,
          exclusions: this.prop.exclusions,
          remarks: this.prop.remarks,
          agreement: this.prop.agreement,
          occupants: this.prop.occupants,
          howShown: this.prop.howShown,
          saleDate: this.prop.saleDate,
          accessRoad: this.prop.accessRoad
        }

        this.saved = true;
        debugger

        this.$store.dispatch('editProperty', newProperty)
      },

    }
  }

</script>

<style scoped>
  .holder {
    background-color: black;
    padding-bottom: 3rem;
    padding-top: 3rem;
    box-shadow: 0px 0px 12px rgba(88, 83, 74, .7);
  }

</style>



