import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Search from '../components/Search'
import SearchView from '../components/SearchView'
import Property from '../components/Property'
import Login from '../components/Login'
import Reset from '../components/Reset'
import Advertising from '../components/Advertising'
import Border from '../components/Border'
import Contact from '../components/Contact'
import Faqs from '../components/Faqs'
import Privacy from '../components/Privacy'
import Resources from '../components/Resources'
import Map from '../components/Map'
import Admin from '../components/Admin'
import Terms from '../components/Terms'
import Lawyers from '../components/Lawyers'
import LakeFront from '../components/LakeFront'
import RiverFront from "../components/RiverFront"
import HuntingLand from "../components/HuntingLand"
import Islands from "../components/Islands"
import CottageCabins from '../components/CottageCabins'
import DevelopmentResorts from '../components/DevelopmentResorts'
import RemoteAccess from '../components/RemoteAccess'
import UnorganizedTownships from '../components/UnorganizedTownships'
import About from '../components/About'
import Tdo from '../components/Tdo'

import AdminDash from '../components/Admin/DashBoard'
import PropList from '../components/Admin/PropList'
import AdminView from '../components/Admin/View'
import AdminCreateProp from '../components/Admin/PropFields'
import UserList from '../components/Admin/UserList'
import AdminCreateUser from '../components/Admin/UserFields'



Vue.use(Router)

export default new Router({

  routes: [{
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/admin',
      name: 'Admin',
      component: AdminDash,
      children: [{
        path: 'view',
        name: 'AdminView',
        component: AdminView,
        children: [{
          path: 'props',
          name: 'AdminPropView',
          component: PropList,
        }, {
          path: 'users',
          name: 'AdminUserView',
          component: UserList,
        }]
      }, {
        path: 'create',
        name: 'AdminCreate',
        component: AdminView,
        children: [{
            path: 'prop',
            name: 'AdminPropCreate',
            component: AdminCreateProp,
          },
          {
            path: 'user',
            name: 'AdminUserCreate',
            component: AdminCreateUser,
          }
        ]
      }, {
        path: 'edit',
        name: 'AdminEdit',
        component: AdminView,
        children: [{
            path: 'prop/:id',
            name: 'AdminPropEdit',
            component: AdminCreateProp,
          },
          {
            path: 'user/:id',
            name: 'AdminUserEdit',
            component: AdminCreateUser,
          }
        ]
      }]
    },
    {
      path: '/properties/:id',
      name: 'Property',
      component: Property
    },
    {
      path: '/reset/:id',
      name: 'Reset',
      component: Reset
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/advertising',
      name: 'Advertising',
      component: Advertising
    },
    {
      path: '/border',
      name: 'Border',
      component: Border
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/faqs',
      name: 'Faqs',
      component: Faqs
    },
    {
      path: '/lakefront',
      name: 'LakeFront',
      component: LakeFront
    },
    {
      path: '/riverfront',
      name: 'RiverFront',
      component: RiverFront
    },
    {
      path: '/huntingland',
      name: 'HuntingLand',
      component: HuntingLand
    },
    {
      path: '/islands',
      name: 'Islands',
      component: Islands
    },
    {
      path: '/cottage&cabins',
      name: 'CottageCabins',
      component: CottageCabins
    },
    {
      path: '/remoteaccess',
      name: 'RemoteAccess',
      component: RemoteAccess
    },
    {
      path: '/development&resorts',
      name: 'DevelopmentResorts',
      component: DevelopmentResorts
    },
    {
      path: '/unorganizedtownships',
      name: 'UnorganizedTownships',
      component: UnorganizedTownships
    },
    {
      path: '/privacy',
      name: 'Privacy',
      component: Privacy
    },
    {
      path: '/map',
      name: 'Map',
      component: Map
    },
    {
      path: '/lawyers',
      name: 'Lawyers',
      component: Lawyers
    },
    // {
    //   path: '/admin',
    //   name: 'Admin',
    //   component: Admin
    // },
    {
      path: '/terms',
      name: 'Terms',
      component: Terms
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/tdo',
      name: 'Tdo',
      component: Tdo
    },
    {
      path: '/resources',
      name: 'Resources',
      component: Resources
    },
    {
        path: '/search',
        name: "Search",
        component: SearchView
    },
    {
      path: '/*',
      name: 'WrongRoute',
      component: Home
    }
  ]
})



