            <div class="col-sm-3">
              <input v-model="prop.zoning" type="text" class="form-control" id="zoning" autocomplete="on">
            </div>
          </div>

          <div class="form-group row">
            <label for="zoning" class="col-sm-2 col-form-label">Road F0rontage</label>
            <div class="col-sm-3">
              <input v-model="prop.roadFrontage" type="text" class="form-control" id="roadFrontage" autocomplete="on">
            </div>
          </div>






          <div class="form-group row">
