              <input v-model="prop.power" type="text" class="form-control" id="power" autocomplete="on">
            </div>
          </div>

          <div class="form-group row">
            <label for="sewer" class="col-sm-2 col-form-label">Sewer</label>
            <div class="col-sm-3">
              <input v-model="prop.sewer" type="text" class="form-control" id="sewer" autocomplete="on">
