            </div>
          </div>

          <div class="form-group row">
            <label for="structure" class="col-sm-2 col-form-label">Structure</label>
            <div class="col-sm-3">
              <input v-model="prop.structure" type="text" class="form-control" id="structure" autocomplete="on">
            </div>
          </div>




          <div class="form-group row">
            <label for="usage" class="col-sm-2 col-form-label">Usage</label>
            <div class="col-sm-3">
