            </div>
          </div>




          <div class="form-group row">
            <label for="legal" class="col-sm-2 col-form-label">Legal Information</label>
            <div class="col-sm-10">
              <p>
                <textarea v-model="prop.legalInformation" name="comment" type="text" id="legalInformation" cols="100" rows="10" class=" form-control tArea2"
