                <textarea v-model="prop.directionsToProp" name="comment" type="text" id="directionsToProp" cols="100" rows="10" class=" form-control tArea2"
                  autocomplete="off" placeholder="Write only directions to the property..."></textarea>
              </p>

            </div>
