<script>
  import axios from 'axios'
  const BASE_URL = 'https://rland.herokuapp.com';

  const STATUS_INITIAL = 0,
    STATUS_SAVING = 1,
    STATUS_SUCCESS = 2,
    STATUS_FAILED = 3;

  export default {
    name: 'adminCreateProp',
    beforeRouteUpdate(to, from, next) {
      // react to route changes...
      // don't forget to call next()
      console.log(to)
      next(vm => {
        // access to component instance via `vm`
        console.log("this", vm)
      })
    },
    data() {
      return {
        uploadedFiles: [],
        uploadError: null,
        uploadedImageName: [],
        currentStatus: null,
        uploadFieldName: 'photos',
        showForm: false,
        // isInitial: true,
        // isSaving: false,
        uploadedImages: [],
        submitted: false,
        saved: false
      }
    },
    created() {
      if (this.$route.params.id) {
        this.$store.dispatch("getProperty", this.$route.params.id)
      }
    },
    mounted() {
      if (this.$route.params.id) {
        this.$store.dispatch("getProperty", this.$route.params.id)

      }
    },
    computed: {
      prop() {
        if (!this.isEditing) {
          return {
            uploadedImages: []
          }
        }
        return this.$store.state.property
      },
      isEditing() {
        return this.$route.params.id != null
      },
      hasPhotos() {
        return true
      },
      isInitial() {
        return this.currentStatus === STATUS_INITIAL;
      },
      isSaving() {
        return this.currentStatus === STATUS_SAVING;
      },
      isSuccess() {
        return this.currentStatus === STATUS_SUCCESS;
      },
      isFailed() {
        return this.currentStatus === STATUS_FAILED;
      }
    },
    methods: {
      route(route) {
        this.$router.push(route)
      },
      onFileChange(e) {

        var files = e.target.files || e.dataTransfer.files;
        if (!files.length)
          return;
        for (var i = 0; i < files.length; i++) {
          var file = files[i];
          this.createImage(file)

        }
      },
      reset() {

        // reset form to initial state
        this.currentStatus = STATUS_INITIAL;
        this.prop.uploadedImages = [];
        this.uploadError = null;
      },
      resetImages() {
        this.uploadedImageName = []
        this.prop.uploadedImages = []
      },
      upload(formData) {
        const url = `${BASE_URL}/photos/upload`;
        return axios.post(url, formData)
          // get data
          .then(x => x.data)
          // add url field
          .then(x => x.map(img => Object.assign({},
            img, {
              url: `${BASE_URL}/images/${img.id}`
            })));
      },
      save(formData) {

        // upload data to the server
        this.currentStatus = STATUS_SAVING;
        this.upload(formData).then(x => {
              console.log("data", x)
            this.prop.uploadedImages = [].concat(x);
            this.currentStatus = STATUS_SUCCESS;
          })
          .catch(err => {
            this.uploadError = err.response;
            this.currentStatus = STATUS_FAILED;
          });
      },
      filesChange(fieldName, fileList) {

        // handle file changes
        var formData = new FormData();

        if (!fileList.length) return;

        // append the files to FormData
        Array
          .from(Array(fileList.length).keys())
          .map(x => {
            formData.append(fieldName, fileList[x], fileList[x].name);
          });

        // save it
        this.save(formData);
      },
      toggleSubmit() {
        this.propertyUpload = true;
        this.propertyEdit = false;
      },

      toggleEdit() {
        this.propertyUpload = false;
        this.propertyEdit = true;

      },
      createProperty() {

        let newProperty = {
          selected: this.prop.selected.toLowerCase().split(" ").join(""),
          name: this.prop.name,
          currency: this.prop.currency,
          listPrice: this.prop.listPrice,
          lotSize: this.prop.lotSize,
          long: this.prop.long,
          lat: this.prop.lat,
          uploadedImages: this.prop.uploadedImages,
          toggle: this.prop.toggle,
          description: this.prop.description,
          taxes: this.prop.taxes,
          year: this.prop.year,
          zoning: this.prop.zoning,
          roadfrontage: this.prop.roadFrontage,
          survey: this.prop.survey,
          seasonalAccess: this.prop.seasonalAccess,
          power: this.prop.power,
          treeRights: this.prop.treeRights,
          frontage: this.prop.frontage,
          structure: this.prop.structure,
          usage: this.prop.usage,
          mineralRights: this.prop.usage,
          unorganizedTownship: this.prop.unorganizedTownship,
          legal: this.prop.legal, //might not need this here
          title: this.prop.title,
          bodyOfWaterName: this.prop.bodyOfWaterName,
          directionsToProp: this.prop.directionsToProp,
          water: this.prop.water,
          sewer: this.prop.sewer,
          legalInformation: this.prop.legalInformation,
          shortDescription: this.prop.description.slice(0, 100), //not sure about this one
          conditionalOffer: this.prop.conditionalOffer, //we don't need this
          isFeature: this.prop.isFeature, //we don't need this
          propertyDisclosure: this.prop.propertyDisclosure, //we don't need this
          use: this.prop.use, //we don't need this
          basement: this.prop.basement, //we don't need this
          siteInfluences: this.prop.siteInfluences, //we don't need this
          numStories: this.prop.numStories, //we don't need this 
          buildingDiv: this.prop.buildingDiv, //we don't need this
          totalArea: this.prop.totalArea, //we don't need this
          area: this.prop.area, //we don't need this 
          officeArea: this.prop.officeArea, //we don't need this 
          whseArea: this.prop.whseArea, //we don't need this
          ceiling: this.prop.ceiling, //we don't need this
          construction: this.prop.construction, //we don't need this     
          heatAir: this.prop.heatAir, //we don't need this
          whseHeatAir: this.prop.whseHeatAir, //we don't need this    
          fuel: this.prop.fuel, //we don't need this  
          loading: this.prop.loading, //we don't need this
          numRentalUnits: this.prop.numRentalUnits, //we don't need this  
          features: this.prop.features, //we don't need this
          mortage: this.prop.mortage, //we don't need this
          exclusions: this.prop.exclusions, //we don't need this
          remarks: this.prop.remarks, //we don't need this
          agreement: this.prop.agreement, //we don't need this
          occupants: this.prop.occupants, //we don't need this    
          howShown: this.prop.howShown, //we don't need this
          saleDate: this.prop.saleDate, //we don't need this
          accessRoad: this.prop.accessRoad, //might not need this here
          sold: this.prop.sold,
          active: this.prop.active,
          inactive: this.prop.inactive
       
        }

        this.submitted = true;
        debugger

        this.$store.dispatch('createProperty', newProperty)
      },
      editProperty() {

        let newProperty = {
          _id: this.$route.params.id,
          selected: this.prop.selected.toLowerCase().split(" ").join(""),
          name: this.prop.name,
          currency: this.prop.currency,
          listPrice: this.prop.listPrice,
          lotSize: this.prop.lotSize,
          long: this.prop.long,
          lat: this.prop.lat,
          uploadedImages: this.prop.uploadedImages,
          toggle: this.prop.toggle,
          description: this.prop.description,
          taxes: this.prop.taxes,
          year: this.prop.year,
          zoning: this.prop.zoning,
          roadfrontage: this.prop.roadFrontage,
          survey: this.prop.survey,
          seasonalAccess: this.prop.seasonalAccess,
          power: this.prop.power,
          treeRights: this.prop.treeRights,
          frontage: this.prop.frontage,
          structure: this.prop.structure,
          usage: this.prop.usage,
          mineralRights: this.prop.usage,
          unorganizedTownship: this.prop.unorganizedTownship, //not sure we need this here
          // remove and move to legalInformation - G legal: this.prop.legal,                                 // do we need this if we are using legalInformation?
          title: this.prop.title,
          bodyOfWaterName: this.prop.bodyOfWaterName,
          directionsToProp: this.prop.directionsToProp,
          water: this.prop.water,
          sewer: this.prop.sewer,
          legalInformation: this.prop.legalInformation,
          shortDescription: this.prop.description.slice(0, 100), //not sure about this one
          conditionalOffer: this.prop.conditionalOffer, //we don't need this
          isFeature: this.prop.isFeature, //we don't need this
          propertyDisclosure: this.prop.propertyDisclosure, //we don't need this 
          use: this.prop.use, //we don't need this
          basement: this.prop.basement, //we don't need this 
          siteInfluences: this.prop.siteInfluences, //we don't need this   
          numStories: this.prop.numStories, //we don't need this
          buildingDiv: this.prop.buildingDiv, //we don't need this
          totalArea: this.prop.totalArea, //we don't need this
          area: this.prop.area, //we don't need this
          officeArea: this.prop.officeArea, //we don't need this 
          whseArea: this.prop.whseArea, //we don't need this
          ceiling: this.prop.ceiling, //we don't need this
          construction: this.prop.construction, //we don't need this
          heatAir: this.prop.heatAir, //we don't need this 
          whseHeatAir: this.prop.whseHeatAir, //we don't need this 
          fuel: this.prop.fuel, //we don't need this
          loading: this.prop.loading, //we don't need this
          numRentalUnits: this.prop.numRentalUnits, //we don't need this   
          features: this.prop.features, //we don't need this
          mortage: this.prop.mortage, //we don't need this
          exclusions: this.prop.exclusions, //we don't need this
          remarks: this.prop.remarks, //we don't need this 
          agreement: this.prop.agreement, //we don't need this 
          occupants: this.prop.occupants, //we don't need this
          howShown: this.prop.howShown, //we don't need this
          saleDate: this.prop.saleDate, //we don't need this
          accessRoad: this.prop.accessRoad //this might not need to be here
          //sold, active and inactive ... are missing if they are supposed to be here
        }

        this.saved = true;
        debugger

        this.$store.dispatch('editProperty', newProperty)
      },

    }
  }

</script>

<style scoped>
  .holder {
    background-color: black;
    padding-bottom: 3rem;
    padding-top: 3rem;
    margin-top: 2rem;
    box-shadow: 0px 0px 12px rgba(88, 83, 74, .7);
  }

  .thumbnail {
    height: 100px;
    margin: 10px;
    float: left;
  }

  #descTextarea {
    margin-left: 2.8rem;
  }

  .descript1 {
    margin-left: 1rem;
  }

  .result {
    border: 4px dotted #cccccc;
    margin: 4px;
  }

  .form-group {
    margin-left: 5rem;
    margin-top: 2rem;
    margin-right: 3rem;
    font-size: 1.1rem;
  }

  .form-control {
    margin-left: 2rem;
  }

  .aiTitle {
    margin-top: 3rem;
    margin-left: 6rem;
  }

  .tArea1 {
    margin-left: -4rem;
    width: 32rem;
  }

  .tArea2 {
    width: 40rem;
  }

  .inputF1 {
    margin-left: 2.9rem;
  }

  .custom-select {
    margin-left: 2.9rem;
  }

  .sa {
    margin-left: -80px;
  }

  #title {
    margin-left: 1.9rem;
  }

  #mineralRights {
    margin-left: -48px;
  }

  #bodyOfWaterName {
    margin-left: -48px;
  }

  .btn {
    margin-left: 6rem;
  }

</style>



