<template>
  <div class="row justify-content-center">
    <div class="col-sm-8" v-for="user in users">
      <div class="row">
        <ul class="list-group">
          <div class="list-group-item flex-column align-items-start">
            <div class="d-flex w-300 justify-content-between">
              <h2 class="mb-1">{{user.name}}</h2>
              <small>{{user.created}} {{user.email}}</small>
              <p>Admin: <b>{{user.admin}}</b></p>
              <p>Last Seen: {{timeConverter(user.lastSeen)}}</p>
            </div>
          </div>
          <div class="list-group-item price">
            <button type="button" class="btn btn-danger">Delete</button>
            <button type="button" class="btn btn-warning" @click="route(user)">Edit</button>
          </div>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'adminPropList',
    data() {
      return {}
    },
    mounted() {
      this.$store.dispatch('getAllUsers')
    },
    computed: {
      users() {
        return this.$store.state.users
      },
      userJoined() {
        if (this.$store.state.users >= 0){
            return []
        }
        let data = []
        this.$store.state.users.array.forEach(function(element) {
            data.push()
        }, this);
      }
    },
    methods: {
      route(prop) {
        this.$router.push({
          name: 'AdminUserEdit',
          params: {
            id: prop._id
          }
        })
      },
      timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec;
        return time;
      }
    }
  }

</script>

<style scoped>
  h2 {
    color: black;
  }

  .list-group {
    width: 100%;
    margin-bottom: 5px;
    color: black;
  }

</style>



