<template>
  <div class="container-fluid page">
    <div class="row">
      <div class="col-md-8">
        <h1>
          <i>Recreationland.net</i>
        </h1>
      </div>
    </div>
    <div class="row justify-content-center">

      <div class="col-md-8">
        <div class="alert alert-danger" role="alert" v-show="hasError">
          <strong>Warning!</strong> {{error}}
        </div>
        <div class="alert alert-info" role="alert" v-show="hasMessage">
            <strong>Info!</strong> {{message}}
          </div>
      </div>
      <div class="col-sm-8">
        <div class=" row justify-content-center">
          <div class="col-sm-12 tab select">
            <a href="#">Reset Password</a>
          </div>
        </div>
      </div>
      <div class="col-sm-8 holder">
        <div id="form-panel">
          <div id="signinbox">
            <form action="#" id="signinform">
              <div class="form-group row">
                <label for="email" class="col-sm-3 col-form-label">New Password</label>
                <div class="col-sm-9">
                  <input v-model="user.password" type="password" class="form-control" id="email" autocomplete="off">
                </div>
              </div>
              <div class="form-group row">
                <label for="password" class="col-sm-3 col-form-label">Conmfirm Password</label>
                <div class="col-sm-9">
                  <input v-model="user.confirmPass" type="password" class="form-control" id="password" autocomplete="off">
                </div>
              </div>

              <input @click.prevent="login" type="submit" name="loginbtn" id="submitlogin" value="Reset Password" class="btn" tabindex="3">
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "reset",
    data() {
      return {
        user: {
          password: "",
          confirmPass: "",
        },
        missing: {

        }
      }
    },
    computed: {
      confirmed() {
        if (!this.enteredPass) {
          return false
        }
        return this.user.password == this.user.confirmPass
      },
      enteredPass() {
        return this.user.confirmPass != ""
      },
      hasFailed() {
        return this.enteredPass != this.confirmed
      },
      hasSelected() {
        return this.user.selected != ""
      },
      hasNotify() {
        return this.user.notify
      },
      hasAll() {
        return (this.user.name != "" && this.user.email != "" && this.user.password != "" && this.confirmed != "" &&
          this.user.selected != "" && this.user.notify)
      },
      hasError() {
        return this.$store.state.errorMessage != ""
      },
      error() {
        return this.$store.state.errorMessage
      },
      hasMessage(){
          return this.$store.state.message != ""
      },
      message() {
          return this.$store.state.message
      }
    },
    methods: {
      login() {
        let data = this.user
        data.validator = this.$route.params.id
        this.$store.dispatch('resetConfirm', data)
      },
    },
    components: {

    }
  }

</script>


<style scoped>
  .holder {
    background-color: black;
    margin-bottom: 5rem;
    box-shadow: 0px 0px 12px rgba(88, 83, 74, .7);
  }

  .shareInfo {
    color: white;
    font-size: 1.2rem;
  }
  .highlight {
    color: #8a1e31;
    font-weight: bold;
  }

  .shareInfo2 {
    color: white;
    font-size: 1.2rem;
    margin-left: 10px;
  }

  p {
    font-size: 1rem;
  }

  .chkbx {
    margin-left: 1px;
    margin-bottom: 10px;
  }

  .page {
    background-color: #3b423a;
    width: cover;
  }

  #tabbox {
    width: 100%;
    height: 40px;
    border-bottom: 1px solid #fff;
  }

  .confirmedPass {
    color: green;
  }

  .failedPass {
    color: red;
  }

  .tab {
    display: block;
    width: 50%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    -webkit-border-top-left-radius: 20px;
    -webkit-border-top-right-radius: 20px;
    text-transform: uppercase;
    background-color: black;
  }

  .tab a {
    text-decoration: none;
    color: orange;
  }

  .select {
    background-color: white;
  }

  label {
    display: block;
    padding: 3px;
    text-transform: uppercase;
    color: white;
  }

  #signinbox {
    padding: 40px;
  }

  .txtfield {
    border-radius: 5px;
    height: 25px;
    width: 70%;
  }

  h1 {
    font-family: 'Abril Fatface', cursive;
    font-size: 52px;
    text-shadow: 2px 2px 2px #fff;
    color: red;
    margin-left: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .btn {
    display: block;
    padding: 10px;
    border-radius: 7px;
    background-color: white;
    color: black;
    font-size: 15px;
    margin: 10px 0px 30px 15px;
    box-shadow: 0px 0px 5px #fff;
  }

  #signupbox {
    padding: 15px;
    margin: 30px 0px 0px 3px;
  }

  #login {
    background-color: white;
  }


  .missing {
    text-shadow: 0 0 3px #FF0000;
  }

</style>



