<template>
  <div class="admin">
    <div class="non-admins" v-if="this.user == null || !this.user.admin">
      <div class="alert alert-danger" role="alert">
        <strong>Oh snap!</strong> Permissions denied, returning you to the home page.
      </div>
    </div>



    <div v-if="this.user && this.user.admin && this.propertyUpload" class="admin1">
      <h1 class="pageTitles text-center">
        <p>
          <b>Recreationland</b>
        </p>
        <p>
          <b>Admin Property Upload</b>
        </p>
      </h1>

      <div class="row">
    <div class="col-6">
      <button type="button" @click="toggleSubmit" class="btn btn-secondary">Property Upload</button>
    </div>
    <div class="col-6">
      <button type="button" @click="toggleEdit" class="btn btn-secondary">Propert Edit</button>
    </div>
  </div>

      <form @submit.prevent="createProperty()">
        <div>
          <div>
            <label class="propType">Property Type:</label>
              <select v-model="prop.selected">
                <option>Lake Front</option>
                <option>River Front</option>
                <option>Hunting Land</option>
                <option>Remote Access</option>
                <option>Islands</option>
                <option>Cottages/Cabins</option>
                <option>Development/Resorts</option>
                <option>Unorganized Township</option>
              </select>
          </div>
        </div>


        <div>
            <div class="form-group1">
              <div class="container">
                  <div class="row">
                      <div class="col-3">
                        <div><label for="formGroupInput" class="label1">Prop Name/Title:</label>
                        <input v-model="name" type="text" class="form-control inputF1" id="name" placeholder="Property Name/Title" />
                      </div>
                      <div>
                        <label for="formGroupInput" class="label1">List Price:</label>
                        <input v-model="listPrice" type="text" class="form-control inputF1" id="listPqice" placeholder="List Price" />
                      </div>
                      <div>
                        <label for="formGroupInput" class="label1">Dollar Type:</label>
                          <select v-model="currency" id="dollar" class="inputF1 form-control">
                           <option>CDN</option>
                           <option>US</option>
                          </select>
                      </div>
                      <div>
                        <label for="formGroupInput" class="label1">Lot Size/Acres:</label>
                        <input v-model="lotSize" type="text" id="lotSize" class="form-control inputF1" placeholder="Lot Size/Acres" />
                      </div>
                      <div>
                        <label for="formGroupInput" class="label1">Longitude:</label>
                        <input v-model="long" class="form-control inputF1" type="text" placeholder="Longitude"/>
                      </div>
                      <div>
                        <label for="formGroupInput" class="label1">Latitude:</label>
                        <input v-model="lat" class="form-control inputF1" type="text" placeholder="Latitude" />
                      </div>
                  </div>
                </div>
              </div>
            </div>



        <div v-if="!image">
          <h2>Select an image</h2>
          <input class="btnChoose" multiple type="file" @change="onFileChange">
        </div>
        <div v-else>
          <img :src="image" />
          <button @click="removeImage">Remove image</button>
        </div>

        <div>
          <div class="saf">
            <label class="sold">Sold</label>
            <input type="checkbox" v-model="sold" />
          </div>
          <div class="saf">
            <label class="active">Active Listing</label>
            <input type="checkbox" v-model="activeListing" />
          </div>
          <div class="saf">
            <label class="inactive">Inactive Listing</label>
            <input type="checkbox" v-model="inactiveListing" />
          </div>
        </div>




        <div>
          <div>
            <h3 class="aiTitle">
              <b>Description:</b>
            </h3>
            <p>
              <textarea v-model="description" name="comment" id="" cols="100" rows="10" class="tArea2" placeholder="Write about the property..."></textarea>
            </p>
          </div>
        </div>
        <div>
          <div>
            <h3 class="aiTitle">
              <b>Additional Information:</b>
            </h3>
          </div>
        </div>

              <div>
                  <div class="sec2">
                    <div class="form-group info">
                      <div class="container-fluid label2">
                        <div class="row">

                          <div class="col-md-3">
                            <div><label for="formGroupInput">Taxes</label>
                              <input v-model="taxes" type="text" class="form-control inputF2" id="taxes" placeholder="Tax Amount">
                            </div>
                            <div><label for="formGroupInput">Year</label>
                              <input v-model="year" type="text" class="form-control inputF2" id="year" placeholder="Year">
                            </div>
                            <div><label for="formGroupInput">Zoning</label>
                              <input v-model="zoning" type="text"class="form-control inputF2"  id="zoning" placeholder="Zoning (current?)">
                            </div>
                            <div><label for="formGroupInput">Road Frontage</label>
                              <input v-model="roadFrontage" type="text" class="form-control inputF2" id="roadFrontage" placeholder="Road for property?">
                            </div>
                          </div>

                          <div class="col-md-3">
                            <div><label for="formGroupInput">Water</label>
                              <input v-model="water" type="text" class="form-control inputF2"  id="water" placeholder="Water (Needs a Well?)">
                            </div>
                            <div ><label for="formGroupInput" >Survey</label>
                              <input v-model="survey" type="text" class="form-control inputF2" id="survey" placeholder="Survey (Y/N?)">
                            </div>
                            <div><label for="formGroupInput">Seasonal Access</label>
                              <input v-model="seasonalAccess" type="text" class="form-control inputF2" id="seasonalAccess" placeholder="ATV/Snowmobile Access?">
                            </div>
                            <div><label for="formGroupInput">Power</label>
                              <input v-model="power" type="text" class="form-control inputF2" id="power" placeholder="Power (Y/N Go Green)">
                            </div>
                          </div>

                          <div class="col-md-3">
                            <div><label for="formGroupInput">Sewer</label>
                              <input v-model="sewer" type="text" class="form-control inputF2" id="sewer" placeholder="Sewer (Needs Septic?)">
                            </div>
                            <div><label for="formGroupInput">Tree Rights</label>
                              <input v-model="treeRights" type="text" class="form-control inputF2" id="treeRights" placeholder="Tree Rights">
                            </div>
                            <div><label for="formGroupInput">Frontage</label>
                              <input v-model="frontage" type="text" class="form-control inputF2" id="frontage" placeholder="Frontage (Water?)">
                            </div>
                            <div><label for="formGroupInput">Structure</label>
                              <input v-model="structure" type="text" class="form-control inputF2" id="structure" placeholder="Structure">
                            </div>
                          </div>

                          <div class="col-md-3">
                            <div><label for="formGroupInput">Usage</label>
                              <input v-model="usage" type="text" class="form-control inputF2" id="usage" placeholder="Usage (Recreation/Hunting?">
                            </div>
                            <div><label for="formGroupInput">Mineral Rights</label>
                              <input v-model="mineralRights" type="text"class="form-control inputF2"  id="mineralRights" placeholder="Mineral Rights (Y/N?)">
                            </div>
                            <div><label for="formGroupInput">Body of Water Name</label>
                              <input v-model="bodyOfWaterName" type="text" class="form-control inputF2" id="bodyOfWaterName" placeholder="Body of Water Name">
                            </div>
                            <div><label for="formGroupInput">Title</label>
                              <input v-model="title" type="text" id="title" class="form-control inputF2" placeholder="Title (Y/N)">
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
            </div>


            <div>
              <h3 class="aiTitle">
                <b>Legal Information:</b>
              </h3>
              <p>
                <textarea v-model="legalInformation" name="legal" id="" cols="3" rows="02" class="tArea1" placeholder="Write only legal info for property..."></textarea>
              </p>
            </div>






            <div>
              <h3 class="aiTitle">
                <b>Directions to Prop:</b>
              </h3>
              <p>
                <textarea v-model="directionsToProp" name="comment" id="" cols="100" rows="10" class="tArea2" placeholder="Write only directions to the property..."></textarea>
              </p>
            </div>
          <div>
            <button class="btn pnt btn-primary" type="Submit">Submit Properties</button>
          </div>
        </div>
      </form>
      <div v-if="submitted" class="success">
        <div class="alert alert-success" role="alert">
          <strong>Success!</strong> property has been uploaded successfully.
        </div>
      </div>

    </div>
  </div>
</template>





<script>
// import { upload } from './file-upload.service';
const STATUS_INITIAL = 0, STATUS_SAVING = 1, STATUS_SUCCESS = 2, STATUS_FAILED = 3;


export default {
  name: 'admin',
  data() {
    return {
      uploadedFiles: [],
      uploadError: null,
      currentStatus: null,
      uploadFieldName: 'photos',
      showForm: false,
      // isInitial: true,
      // isSaving: false,
      name: "",
      min: "",
      max: "",
      selected: "",
      propertyName: "",
      listPrice: "",
      currency: 0,
      lotSize: "",
      long: "",
      lat: "",
      uploadedImages: [],
      sold: false,
      activeListing: false,
      inactiveListing: false,
      feature: false,
      description: "",
      taxes: 0,
      year: 0,
      zoning: "",
      roadFrontage: "",
      water: "",
      survey: "",
      seasonalAccess: "",
      power: "",
      sewer: "",
      treeRights: "",
      frontage: "",
      structure: "",
      usage: "",
      mineralRights: "",
      unorganizedTownship: "",
      image: "",
      submitted: false,
      legal: "",
      title: "",
      bodyOfWaterName: "",
      directionsToProp: "",
      legalInformation: "",
      propertyUpload: true,
      propertyEdit: false,

    }
  },
  
  // mounted() {
  //   this.$store.dispatch('searchProperties', this.query)
  // },
  mounted() {
    let hc = this;
    this.$store.dispatch('getAuth')
    this.reset();
    if (this.user == null || !this.user.admin) {
      setTimeout(function () {
        hc.$store.dispatch("returnHome")
      }, 5000);
    }
  },
  computed: {
    properties() {
      return this.$store.state.properties
    },
    user() {
      return this.$store.state.user
    },
    isInitial() {
      return this.currentStatus === STATUS_INITIAL;
    },
    isSaving() {
      return this.currentStatus === STATUS_SAVING;
    },
    isSuccess() {
      return this.currentStatus === STATUS_SUCCESS;
    },
    isFailed() {
      return this.currentStatus === STATUS_FAILED;
    }
  },
  methods: {
    onFileChange(e) {

      var files = e.target.files || e.dataTransfer.files;
      if (!files.length)
        return;
      for (var i = 0; i < files.length; i++) {
        var file = files[i];
        this.createImage(file)

      }
    },
    createImage(file) {

      var image = new Image();
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.uploadedImages.push(e.target.result)
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage: function (e) {
      this.image = '';
    },

    reset() {

      // reset form to initial state
      this.currentStatus = STATUS_INITIAL;
      this.uploadedFiles = [];
      this.uploadError = null;
    },
    save(formData) {

      // upload data to the server
      this.currentStatus = STATUS_SAVING;

      this.$store.dispatch("upload", formData)
        .then(x => {

          this.uploadedFiles = [].concat(x);
          this.currentStatus = STATUS_SUCCESS;
        })
        .catch(err => {
          this.uploadError = err.response;
          this.currentStatus = STATUS_FAILED;
        });
    },
    filesChange(fieldName, fileList) {

      // handle file changes
      var formData = new FormData();

      if (!fileList.length) return;

      // append the files to FormData
      Array
        .from(Array(fileList.length).keys())
        .map(x => {
          formData.append(fieldName, fileList[x], fileList[x].name);
        });

      // save it
      this.save(formData);
    },
    toggleSubmit(){
      this.propertyUpload = true;
      this.propertyEdit = false;
    },

    toggleEdit(){
      this.propertyUpload = false;
      this.propertyEdit = true;

    },
    createProperty() {

      let newProperty = {
        selected: this.selected.toLowerCase().split(" ").join(""),
        name: this.name,
        currency: this.currency,
        lotSize: this.lotSize,
        long: this.long,
        lat: this.lat,
        uploadedImages: this.uploadedImages,
        toggle: this.toggle,
        description: this.description,
        taxes: this.taxes,
        year: this.year,
        zoning: this.zoning,
        roadfrontage: this.roadFrontage,
        survey: this.survey,
        seasonalAccess: this.seasonalAccess,
        power: this.power,
        treeRights: this.treeRights,
        frontage: this.frontage,
        structure: this.structure,
        usage: this.usage,
        mineralRights: this.usage,
        unorganizedTownship: this.unorganizedTownship,
        legal: this.legal,
        title: this.title,
        bodyOfWaterName: this.bodyOfWaterName,
        directionsToProp: this.directionsToProp,
        water: this.water,
        sewer: this.sewer,
        legalInformation: this.legalInformation,
        shortDescription: this.description.slice(0,100)
      }

      this.submitted = true;
      debugger

      this.$store.dispatch('createProperty', newProperty)
    },
    searchProperties() {
      for (var prop in this.propertyModel) {
        if (this.propertyModel[prop].value) {
          // { $lt: this.propertyModel[prop].value }
          if (prop == 'salePrice' || prop == 'totalAcreage') {
            this.newProperty[prop] = { "$gte": this.min, "$lt": this.max }
          } else {
            this.newProperty[prop] = this.propertyModel[prop].value
          }
        }
      }
      this.$store.dispatch('searchProperties', this.newProperty)
      this.newProperty = {}
      this.min = 0
      this.max = 0
    },
    checkboxProperties(minValue, maxValue) {
      this.propertyModel.salePrice.value = true
      this.min = minValue
      this.max = maxValue
    },
    transferUrl(url) {
      this.propertyModel.propertyUrl.value = url
    }
  },
  components: {}
}
</script>


<style scoped>

.form-group1{
  margin-left: 10px;
}

.form-group{
  display: block;
  width: 80%;
  padding: 8rem;
}

.propName{
  width: 450px;
}

.propType{
  margin-left: 148px;
  margin-bottom: 20px;
}

.inputF1{
  margin-left: 20px;
  margin-bottom: 25px;
  margin-top: 5px;
  padding-top: 15px;
}

.inputF2{
  margin-bottom: 25px;
  margin-top: 5px;
  padding-top: 15px;
}

.aiTitle{
  margin-bottom: 10px;
  margin-top: 15px;
  margin-left: 145px;
}

.label1{
  margin-left: 20px;
  margin-bottom: 5px;
}

.label2{
  margin-top: 10px;
}

.info{
  margin:-110px 20px 0px 0;
}

/*.optbox1{
  margin-left: 250px;
}*/

.admin {
  background-color: orange;
  height: 2800px;
}

.active{
  padding-bottom: 20px;
}

