  margin-left: 300px;
  color: white;
  background-color: blue;
  border-radius: 10px;
}


.btn {
  margin-bottom: 20px;
  margin-left: 140px;
  margin-right: 5px;
  height: 35px;
  background-color: red;
}

.btnChoose{
  margin-left: 140px;
  padding-top: 5px;
  padding-bottom: 20px;
}

h2{
  padding-top: 25px;
  margin-left: 140px;
}

h3 {
  margin-left: 140px;
}

.saf {
  margin-left: 140px;
}

.sel{
  margin-bottom: 25px;
  padding-bottom: 15px;
}

.sec2{
  height: 600px;
}

.tArea1 {
  margin-top: 5px;
  height: 110px;
  width: 450px;
  margin-left: 140px;
  margin-bottom: 50px;
}

.tArea2 {
  height: 110px;
