  border-color: blue;
  background-color: white;
  border-radius: 5px;
  color: green;
  width: 200px;
  height: 50px;
  margin-left: 310px;
}*/

.edProp {
  width: 500px;
  margin-top: 35px;
}

.dropbox {
  outline: 2px dashed grey;
  /* the dash box */
  outline-offset: -10px;
  background: lightcyan;
  color: dimgray;
  padding: 10px 10px;
  min-height: 200px;
  /* minimum height */
  position: relative;
