<template>
  <div class="container-fluid page">
    <div class="row">
      <div class="col-md-8">
        <h1>
          <i>Recreationland.net</i>
        </h1>
      </div>
    </div>
    <div class="row justify-content-center">

      <div class="col-md-8">
        <div class="alert alert-danger" role="alert" v-show="hasError">
          <strong>Warning!</strong> {{error}}
        </div>
        <div class="alert alert-info" role="alert" v-show="hasMessage">
            <strong>Info!</strong> {{message}}
          </div>
      </div>
      <div class="col-sm-8">
        <div class=" row justify-content-center">
          <div class="col-sm-6 tab" @click.prevent="swapTab('login')" v-bind:class="{ select: isLogin}">
            <a href="#">Login</a>
          </div>
          <div class="col-sm-6 tab" @click.prevent="swapTab('login')" v-bind:class="{ select: !isLogin}">
            <a href="#" id="register">Register</a>
          </div>
        </div>
      </div>
      <div class="col-sm-8 holder">

        <div id="form-panel">
          <div id="signinbox" v-show="isLogin">
            <form action="#" id="signinform">
              <div class="form-group row" v-show="!reset">
                <label for="email" class="col-sm-2 col-form-label">Email</label>
                <div class="col-sm-10">
                  <input v-model="user.email" type="email" class="form-control" id="email" autocomplete="off">
                </div>
              </div>
              <div class="form-group row" v-show="!reset">
                <label for="password" class="col-sm-2 col-form-label">Password</label>
                <div class="col-sm-10">
                  <input v-model="user.password" type="password" class="form-control" id="password" autocomplete="off">
                </div>
              </div>
              <div class="form-group row" v-show="!reset">
                <a href="#" class="highlight" @click.prevent="openReset">Forgot Password?</a>
              </div>
              <input v-show="!reset" @click.prevent="login" type="submit" name="loginbtn" id="submitlogin" value="LOGIN" class="btn" tabindex="3">
              <div class="form-group row" v-show="reset">
                <label for="email" class="col-sm-2 col-form-label">Email</label>
                <div class="col-sm-10">
                  <input v-model="user.email" type="email" class="form-control" id="email" autocomplete="off">
                </div>
              </div>
              <input v-show="reset" @click.prevent="resetPassword" type="submit" name="resetbtn" id="submitlogin" value="RESET PASSWORD" class="btn" tabindex="3">
            </form>
          </div>

          <div id="signupbox" v-if="!isLogin">
            <form action="#" id="signupform">
              <div class="form-group row">
                <label for="username" class="col-sm-2 col-form-label" :class="{missing: missing.name}">User Name</label>
                <div class="col-sm-10">
                  <input v-model="user.name" type="text" class="form-control" id="username" autocomplete="off">
                </div>
              </div>
              <div class="form-group row">
                <label for="email" class="col-sm-2 col-form-label" :class="{missing: missing.email}">Email</label>
                <div class="col-sm-10">
                  <input v-model="user.email" type="email" class="form-control" id="email" autocomplete="off">
                </div>
              </div>

              <div class="form-group row">
                <label for="password" class="col-sm-2 col-form-label" :class="{confirmedPass: confirmed, failedPass: hasFailed, missing: missing.password}">Password</label>
                <div class="col-sm-10">
                  <input v-model="user.password" type="password" class="form-control" id="password" autocomplete="off">
                </div>
              </div>

              <div class="form-group row">
                <label for="pacomfirmssword" class="col-sm-2 col-form-label" :class="{confirmedPass: confirmed, failedPass: hasFailed, missing: missing.confirmPass}">Comfirm Password</label>
                <div class="col-sm-10">
                  <input v-model="user.confirmPass" type="comfirm" class="form-control" id="password" autocomplete="off">
                </div>
                <small v-show="hasFailed" id="fileHelp" class="form-text  failedPass">Warning your passwords must match!</small>
              </div>

              <p class="shareInfo" :class="{missing: missing.selected}">To better serve you, please share your status with us:<br> You are a...?</p>

              <div>
                <select v-model="user.selected">
                                    <!-- added this v-model-->
                                    <option>Developer looking for land</option>
                                    <option>Realtor just diggin' aound</option>
                                    <option>Future buyer/seller</option>
                                    <option>Business looking to advertise</option>
                                </select>
              </div><br>

              <div class="form-group row">
                <label for="notify" class="col-sm-12 col-form-label checkbox-inline shareInfo2">Check box to be notified by email when new listings occur.</label>
                <div class="col-sm-1">
                  <input v-model="user.notify" type="checkbox" class="form-control chkbx" id="notify"  autocomplete="off" value="notify checked"> <!--added  value=notify and checked... not sure if correct-->
                </div>
              </div>

              <input @click.prevent="register" type="submit" name="regbtn" id="submitreg" value="REGISTER" class="btn" tabindex="9">
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "login",
    data() {
      return {
        isLogin: true,
        status: "login",
        error: "",
        //messagemessage: "",
        reset: false,
        user: {
          name: "",
          email: "",
          password: "",
          confirmPass: "",
          selected: "", //I added this for the selected field above
          notify: ""   //not sure what should be here... a string or?? 
        },
        missing: {

        }
      }
    },
    computed: {
      confirmed() {
        if (!this.enteredPass) {
          return false
        }
        return this.user.password == this.user.confirmPass
      },
      enteredPass() {
        return this.user.confirmPass != ""
      },
      hasFailed() {
        return this.enteredPass != this.confirmed
      },
      hasSelected() {
        return this.user.selected != ""
      },
      hasNotify() {
        return this.user.notify  //Does this need a !=this.true 
      },
      hasAll() {
        return (this.user.name != "" && this.user.email != "" && this.user.password != "" && this.confirmed != "" &&
          this.user.selected != "" && this.user.notify)
      },
      hasError() {
        return this.error != ""
      },
      hasMessage(){
          return this.$store.state.message != ""
      },
      message() {
          return this.$store.state.message
      }
    },
    methods: {
      login() {

        this.$store.dispatch('login', this.user)
      },
      register() {
        if (!this.hasAll) {
          this.error = "You must fill in all fields to continue!"
          this.highlightNeeded()
          return
        }
        this.$store.dispatch('register', this.user)
      },
      swapTab() {
        this.isLogin = !this.isLogin
      },
      setTab(tab) {
        this.status = tab
      },
      openReset() {
        this.reset = true
      },
      highlightNeeded() {
        let data = this.user
        for (var key in data) {
          if (!data.hasOwnProperty(key)) continue;

          let obj = data[key]
          if (obj == "" || obj == false) {
            this.missing[key] = true
          }
        }
      },
      resetPassword(){
        this.$store.dispatch('reset', this.user)
      }
    },
    components: {

    }
  }

</script>


<style scoped>
  .holder {
    background-color: black;
    margin-bottom: 5rem;
    box-shadow: 0px 0px 12px rgba(88, 83, 74, .7);
  }

  .shareInfo {
    color: white;
    font-size: 1.2rem;
  }
  .highlight {
    color: #8a1e31;
    font-weight: bold;
  }

  .shareInfo2 {
    color: white;
    font-size: 1.2rem;
    margin-left: 10px;
  }

  p {
    font-size: 1rem;
  }

  .chkbx {
    margin-left: 1px;
    margin-bottom: 10px;
  }

  .page {
    background-color: #3b423a;
    width: cover;
  }

  #tabbox {
    width: 100%;
    height: 40px;
    border-bottom: 1px solid #fff;
  }

  .confirmedPass {
    color: green;
  }

  .failedPass {
    color: red;
  }

  .tab {
    display: block;
    width: 50%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    -webkit-border-top-left-radius: 20px;
    -webkit-border-top-right-radius: 20px;
    text-transform: uppercase;
    background-color: black;
  }

  .tab a {
    text-decoration: none;
    color: orange;
  }

  .select {
    background-color: white;
  }

  label {
    display: block;
    padding: 3px;
    text-transform: uppercase;
    color: white;
  }

  #signinbox {
    padding: 40px;
  }

  .txtfield {
    border-radius: 5px;
    height: 25px;
    width: 70%;
  }

  h1 {
    font-family: 'Abril Fatface', cursive;
    font-size: 52px;
    text-shadow: 2px 2px 2px #fff;
    color: red;
    margin-left: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .btn {
    display: block;
    padding: 10px;
    border-radius: 7px;
    background-color: white;
    color: black;
    font-size: 15px;
    margin: 10px 0px 30px 15px;
    box-shadow: 0px 0px 5px #fff;
  }

  #signupbox {
    padding: 15px;
    margin: 30px 0px 0px 3px;
  }

  #login {
    background-color: white;
  }


  .missing {
    text-shadow: 0 0 3px #FF0000;
  }

</style>



