<template>
  <div class="row justify-content-center">
    <div class="col-xs-12" v-for="propType in types">
      <div class="card">
        <img class="card-img img-responsive" :src="propType.img" alt="Card image">
        <div class="card-img-overlay">
          <router-link :to="propType.url">
            <h4 class="card-title">{{propType.type}}</h4>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  export default {
    name: 'Types',
    data() {
      return {
        types: [{
          img: 'https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/100_4733 (2) LF.jpg',
          type: 'Lake Front',
          url: "lakefront"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/1426710859_lg RF.jpg",
          type: "River Front",
          url: "riverfront"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/1488212429_lg HL.jpg",
          type: "Hunting Land/Large Acreage",
          url: "huntingland"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/pic 14 (2) Remote.jpg",
          type: 'Islands',
          url: "islands"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/1288384066_lg Cottage.jpg",
          type: "Cottages/Cabins",
          url: "cottage&cabins"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/1464281988_lg Resort.jpg",
          type: "Remote Access",
          url: "remoteaccess"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/1355346997_lg DL.jpg",
          type: 'Development/Resorts',
          url: "development&resorts"
        }, {
          img: "https://raw.githubusercontent.com/Jscott321/rland/master/www/static/Properties-img/Unorg TWN..jpg",
          type: "Unorganized Townships",
          url: "unorganizedtownships"
        }]
      }
    },
    computed: {},
    methods: {},
    components: {}
  }

</script>


<style scoped>
  .card-img {
    width: 15rem;
    height: 190px;
  }

  .card {
    margin-top: 30px;
    margin-bottom: 60px;
    margin-left: 40px;
    margin-right: 30px;
  }

  /* .p-2 {
  padding: .1rem;
} */
  /*.img-responsive {
  height: 200px;
  width: 275px;
}*/

  h4 {
    text-align: center;
    top: 80px;
    color: White;
    z-index: 10;
  }



  hr2 {
    margin-left: 1.5rem;
    margin-right: 1rem;
    margin-top: 25px;
    border-width: 3px;
    padding-bottom: 5px;
    background-color: #f5a608;
  }

  .types {
    margin-top: 10px;
    margin-bottom: 40px;
    display: inline-block;
    vertical-align: top;
    width: 100%;
    height: 495px;
  }

  .property-type img {
    background: #fff;
    padding-bottom: 10px;
  }

  .card-deck {
    text-align: left;
    margin-left: 30px;
    margin-right: 15px;
  }

  .property-type h3 {
    text-shadow: 1px 1px 1px #000;
  }

</style>



